<template>
  <div id="signin">
    <div class="form">
      <form class="login-form" @submit.prevent="onSubmit">
        <div class="input">
          <input
                  type="email"
                  placeholder="Email"
                  id="email"
                  v-model="email">
        </div>
        <div class="input">
          <input
                  type="password"
                  placeholder="Password"
                  id="password"
                  v-model="password">
        </div>
        <div class="submit">
          <button type="submit">Login</button>
          <p class="message">Not registered? <router-link to="/signup">Sign Up</router-link> </p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      onSubmit () {
        const formData = {
          email: this.email,
          password: this.password,
        }
        this.$store.dispatch('login', {email: formData.email, password: formData.password})
      }
    }
  }
</script>

